function calculateDraw(squares, row, col) {
    let dem = 0;
    for (let i = 0; i < row * col; i++) {
        if (squares[i] === 'X' || squares[i] === 'O')
            dem++;
    }
    if (dem === row * col) return true;
    return false;
}
export default Game;