//import React from 'react';
import Square from './Square';

function Board({ squares,onClick,winner,rowBoard,colBoard }) {
    return (
        <div>
            {render(squares,rowBoard,colBoard,winner,onClick)}
        </div>
    );
}

/**
 * Hàm trả về ô và tô màu nếu hàng đó thắng
 * @param {*} i : position in squares
 * @param {*} value : {X,0,null}
 * @param {*} winner : true or false square
 * @param {*} onClick 
 * @returns Squares Component
 */
function renderSquare(i,value,winnerSquare,onClick) {
    
    return (
        <Square
            value={value}
            onClick={() => onClick(i)}
            winnerSquare={winnerSquare}
        />
    );
}

/**
 * reder ra cái board
 * @param {*} squares : board of squares
 * @param {*} rowBoard : row number of board
 * @param {*} colBoard : column number of board
 * @param {*} winner : who is winner, array of win squares
 * @param {*} onClick 
 * @returns 
 */
function render(squares,rowBoard,colBoard,winner,onClick) {
    let boardSquare = [];
    let keyNum = 0;
    for (let row = 0; row < rowBoard; row++) {
        let boardRow = [];
        for (let col = 0; col < colBoard; col++) {
            let winnerSquare = winner && winner.winnerLine.includes(keyNum) ? true : false;
            boardRow.push(<span key={keyNum}>{renderSquare(keyNum,squares[keyNum],winnerSquare,onClick)}</span>)
            keyNum++;
        }
        boardSquare.push(<div className="board-row" key={row}>{boardRow}</div>)
    }
    return boardSquare;
}
export default Board;